import React,{useState} from 'react';
import {Card,Button} from 'react-bootstrap';
import axios from 'axios';
import "./MenuCard.css";

function MenuCard({activate,showBtn,menuName,imgPath,price,sales,remain}){
    const [acti,setActi]=useState(activate);
    
    const card1=<Card className="card1" border="gray">
                 <Card.Img variant="top" src={imgPath}></Card.Img>
                <Card.Body>
                <Card.Title className="card_menuName">{menuName}</Card.Title>
                 <Card.Text>
                 {price}원 <br></br>
                 누적판매량: <b>{sales}</b> <br></br>
                 재고량: <b>{remain}</b><br></br>
                 {showBtn?(<Button size="sm" variant='danger'onClick={()=>{
                      function changeActivate(){
                        axios.get('https://every-server.herokuapp.com/api/menuActivate',{params:{menuName:menuName,activate:0}}).then(res=>{
                            if(res.data.success===true){
                                setActi(0);
                            }else{alert('오류발생');}
                        })
                      }
                      changeActivate();
                 }}>비활성화</Button>):(<></>)}
                 </Card.Text>
                 </Card.Body>
                 </Card>;
    const card2= <Card className="card2" border="gray">
                  <Card.Img variant="top" src={imgPath}></Card.Img>
                  <Card.Body>
                   <Card.Title className="card_menuName" style={{opacity:"0.5"}}>{menuName}</Card.Title>
                   <Card.Text>
                    <span style={{opacity:"0.5"}}>
                    {price}원 <br></br>
                     누적판매량: <b>{sales}</b> <br></br>
                     재고량: <b>{remain}</b><br></br>
                    </span>
                    {showBtn?(<Button size="sm" variant='info' onClick={()=>{
                      function changeActivate2(){
                        axios.get('https://every-server.herokuapp.com/api/menuActivate',{params:{menuName:menuName,activate:1}}).then(res=>{
                            if(res.data.success===true){
                                setActi(1);
                            }else{alert('오류발생');}
                        })
                      }
                      changeActivate2();
                 }}>활성화</Button>):(<></>)}
                    </Card.Text>
                    </Card.Body>
                    </Card>;
                
  return(
      <div>
        {acti===1?card1:card2}
      </div>
  );   
}

export default MenuCard;